@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body {
    background: #1e2020;
}

.toggle__dot {
    top: -.25rem;
    left: -.25rem;
    transition: all 0.3s ease-in-out;
}

input:checked~.toggle__dot {
    transform: translateX(100%);
    background-color: #648f1b;
}